/* Colors
-------------------
All colors used throughout the theme
------------------- */
:root {
  --color__accent: oklch(50% 0.25 270deg);
  --color__accent-secondary: oklch(50% 0.25 310deg);
  --color__hue: 270deg;
  --color__hue-secondary: 310deg;
  --color__lightness: 50%;
  --color__lightness-secondary: 50%;
  --color__chroma: 0.25;
  --color__chroma-secondary: 0.25;
  --color__background: white;
  --color__background__alt: oklch(97% 0 0deg);
  --color__background__widget: oklch(95% 0 0deg);
  --color__background__contrast: oklch(15% 0 0deg);
  --color__background__contrast__widget: oklch(8% 0 0deg);
  --color__background__contrast__alt: oklch(2% 0 0deg);
  --color__background__gradient: linear-gradient(180deg, transparent 0, white 100%);
  --color__overlay: oklch(0% 0 0deg / 0.5);
  --color__text: black;
  --color__text-alt: oklch(25% 0 0deg);
  --color__text__contrast: white;
  --color__text__contrast__alt: oklch(73% 0 0deg);
}

/* Fonts
---------------
Fonts
--------------- */
:root {
  --font__sans-serif: "Noto Sans", system-ui, sans-serif;
  --font__serif: "Noto Serif", Charter, Georgia, serif;
  --font__mono: "Fira Code", ui-monospace, monospace;
  --font__body: var(--font__sans-serif);
  --font__headings: var(--font__sans-serif);
  --font__meta: var(--font__sans-serif);
  --font__captions: var(--font__serif);
  --font__quotes: var(--font__serif);
  --font__code: var(--font__mono);
  --font__size: 1.6rem;
  --font__size-base: 1.4rem;
}

:root {
  --font__dynamic-scaling: 0.3vw;
  --font__ratio: 1.25;
  --font__dynamic-ratio: 1.3;
  --font__size__heading-4: calc(
  	(var(--font__size-base) * var(--font__ratio)) +
  		(var(--font__dynamic-scaling) * var(--font__dynamic-ratio))
  );
  --font__size__heading-3: calc(
  	(var(--font__size-base) * var(--font__ratio) * var(--font__ratio)) +
  		(var(--font__dynamic-scaling) * var(--font__dynamic-ratio) * var(--font__dynamic-ratio))
  );
  --font__size__heading-2: calc(
  	(var(--font__size-base) * var(--font__ratio) * var(--font__ratio) * var(--font__ratio)) +
  		(
  			var(--font__dynamic-scaling) * var(--font__dynamic-ratio) *
  				var(--font__dynamic-ratio) * var(--font__dynamic-ratio)
  		)
  );
  --font__size__heading-1: calc(
  	(
  			var(--font__size-base) * var(--font__ratio) * var(--font__ratio) *
  				var(--font__ratio) * var(--font__ratio)
  		) +
  		(
  			var(--font__dynamic-scaling) * var(--font__dynamic-ratio) *
  				var(--font__dynamic-ratio) * var(--font__dynamic-ratio) *
  				var(--font__dynamic-ratio)
  		)
  );
}

/* Sizes
---------------
Sizes, margins and paddings
--------------- */
:root {
  --max-width__base: 130ch;
  --max-width__content: 90ch;
  --margin__tiny: 0.5rem;
  --margin__small: 1rem;
  --margin__medium: 2rem;
  --margin__large: 4rem;
  --padding__tiny: 0.5rem;
  --padding__small: 1rem;
  --padding__medium: 2rem;
  --padding__large: 4rem;
  --header__height: 6rem;
}

