   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  vertical-align: baseline;
  margin: 0;
  border: 0;
  padding: 0;
  font: inherit;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/**
 * GHColors theme by Avi Aryan (http://aviaryan.in)
 * Inspired by Github syntax coloring
 */
code[class*=language-],
pre[class*=language-] {
  color: oklch(34% 0.01 114deg);
  font-size: 0.9em;
  line-height: 1.2em;
  font-family: "Consolas", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-break: normal;
  word-spacing: normal;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
}

pre > code[class*=language-] {
  font-size: 1em;
}

pre[class*=language-]::-moz-selection,
pre[class*=language-] ::-moz-selection,
code[class*=language-]::-moz-selection,
code[class*=language-] ::-moz-selection {
  background: oklch(86% 0.066 253deg);
}

pre[class*=language-]::selection,
pre[class*=language-] ::selection,
code[class*=language-]::selection,
code[class*=language-] ::selection {
  background: oklch(86% 0.066 253deg);
}

/* Code blocks */
pre[class*=language-] {
  margin: 0.5em 0;
  border: 1px solid oklch(90% 0 0deg);
  background-color: white;
  padding: 1em;
  overflow: auto;
}

/* Inline code */
:not(pre) > code[class*=language-] {
  border: 1px solid oklch(90% 0 0deg);
  background: oklch(98% 0 0deg);
  padding: 0.2em;
  padding-top: 1px;
  padding-bottom: 1px;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: oklch(68% 0.024 107deg);
  font-style: italic;
}

.token.namespace {
  opacity: 0.7;
}

.token.string,
.token.attr-value {
  color: oklch(60% 0.233 5deg);
}

.token.punctuation,
.token.operator {
  color: oklch(34% 0.01 114deg); /* no highlight */
}

.token.entity,
.token.url,
.token.symbol,
.token.number,
.token.boolean,
.token.variable,
.token.constant,
.token.property,
.token.regex,
.token.inserted {
  color: oklch(68% 0.102 193deg);
}

.token.atrule,
.token.keyword,
.token.attr-name,
.language-autohotkey .token.selector {
  color: oklch(67% 0.137 231deg);
}

.token.function,
.token.deleted,
.language-autohotkey .token.tag {
  color: oklch(43% 0.174 27deg);
}

.token.tag,
.token.selector,
.language-autohotkey .token.keyword {
  color: oklch(32% 0.22 264deg);
}

.token.important,
.token.function,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

:root {
  accent-color: var(--color__accent);
}

html {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-size: 62.5%;
}

body {
  display: flex;
  flex: 1;
  flex-direction: column;
  background: var(--color__background);
  height: 100%;
  min-height: 100%;
  overflow-y: scroll;
  color: var(--color__text);
  font-weight: 400;
  font-size: var(--font__size);
  line-height: 1.6;
  font-family: var(--font__body);
}
body main {
  flex: 1;
}

* {
  box-sizing: border-box;
  min-width: 0;
}

p,
ul,
ol,
blockquote,
button,
canvas,
caption,
col,
colgroup,
dd,
dl,
dt,
embed,
fieldset,
figcaption,
figure,
img,
form,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 1em;
  margin-bottom: 0.5em;
}

code {
  color: oklch(var(--color__lightness) var(--color__chroma) var(--color__hue));
  font-family: var(--font__code);
}

details summary {
  cursor: pointer;
}
details summary h2,
details summary h3,
details summary h4,
details summary h5,
details summary h6 {
  display: inline;
}

figure > br {
  display: none;
}
figure figcaption {
  margin: 0;
  font-style: italic;
  font-family: var(--font__captions);
  text-align: center;
}
@media (min-width: 55em) {
  figure {
    margin-top: var(--margin__medium);
    margin-bottom: var(--margin__medium);
  }
}
figure.-full > picture img,
figure.-full > img,
figure.-full iframe,
figure.-full > div {
  position: relative;
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw); /* 50vw equals half the width */
  width: 100vw; /* Or whatever max width is */
  max-width: none;
}
figure.-pixels {
  image-rendering: crisp-edges;
}
figure.-wide {
  --card__extra-width: 0rem;
}
@media (min-width: 55em) {
  figure.-wide {
    margin-top: var(--margin__medium);
    margin-bottom: var(--margin__medium);
  }
}
@media (min-width: 65em) {
  figure.-wide {
    --card__extra-width: 10rem;
  }
  figure.-wide > picture img,
  figure.-wide > img,
  figure.-wide iframe,
  figure.-wide > div,
  figure.-wide > figure.gallery-row {
    margin-left: calc(50% - (var(--max-width__content) + var(--card__extra-width)) / 2);
    width: calc(var(--max-width__content) + var(--card__extra-width));
    max-width: none;
  }
}
@media (min-width: 90em) {
  figure.-wide {
    --card__extra-width: 35rem;
  }
}
figure.embed {
  width: 100%;
}
figure.embed iframe {
  aspect-ratio: 16/9;
  min-width: 100%;
  max-width: 100%;
  height: auto;
}
@media (min-width: 65em) {
  figure.embed iframe {
    max-width: none;
  }
}
figure.map iframe {
  min-width: 100%;
  height: 50vh;
}
figure .instagram-media {
  margin: 0 auto !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3;
  font-family: var(--font__headings);
  text-wrap: balance;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
