  text-align: center;
}
@media (min-width: 40em) {
  q {
    margin-top: var(--margin__large);
    margin-bottom: var(--margin__medium);
    margin-left: calc(0% - var(--margin__medium));
    width: calc(100% + var(--margin__large));
    max-width: calc(100% + var(--margin__large));
  }
}
q:before, q:after {
