}

hr {
  margin: var(--margin__medium) 0 0;
  border: 0;
  background: var(--color__background__alt);
  height: 0.4rem;
}

abbr {
  border-bottom-width: 1px;
  border-bottom-style: dotted;
  border-bottom-color: var(--color__text);
