  color: var(--color__text__contrast);
}
.Card.-align-bottom {
  justify-content: flex-end;
  padding-top: 8rem;
}
.Card.-align-top {
  justify-content: flex-start;
}
.Card .tags .tag:not(:first-of-type) {
  display: none;
}
@media (min-width: 55em) {
  .Card.post:first-of-type {
    grid-column-start: span 3;
    aspect-ratio: 21/9;
  }
